import React, {Component} from 'react'
import Typed from 'typed.js';

export default class Home extends Component{
    state={
        strings:['Get all your printing done and delivered to you at little cost', 'It starts at N50 ^1000...prices depend on distance', 'Plus its sooo easy &#128077;^3000', 'Get all your printing done and delivered to you at little cost'],
        typeSpeed: 100,
        loop:true
    }

    componentDidMount() {
        var typed = new Typed("#changes",this.state)
    }

    render() {
        return (
            <div className="intro show"  >
                <h2 className="hero-story accent" >
                    Print from the comfort of your space
                </h2>
                <span className="accent explain" id="changes">
        Get all your printing done and delivered to you at little cost
    </span> <br/>
                <br/>
                    <br/>

                        <button id="toggle" className="btn btn-primary">Lets Go </button>

            </div>
        )
    }
}