import React,{Component} from 'react'



export  default class Canvas extends Component{
 state={
     running:true
 }

 componentDidMount (){







         class Ele {
             constructor(type) {
                 this.type = document.querySelector(type)
             }
             /**
              *
              * @param {object|undefined} size
              */
             dimensions(size) {
                 if(size) {
                     this.type.width = size.x;
                     this.type.height = size.y
                 }
                 return {
                     x: this.type.width || this.type.clientWidth,
                     y: this.type.height || this.type.clientHeight
                 }
             }
             on(event, fn) {

                 this.type? this.type.addEventListener(event, fn): window.addEventListener(event, fn)


             }


         }

         function getIt (name) {
             return new Ele(name)
         }

         window.Coptions = {
             strokeStyle:'#f3cbf9'
         }


         let canvas =  getIt('#canvas')
         let cDiv = getIt('.canvas-div')
         let c = canvas.type.getContext('2d'),
             dim =  canvas.dimensions(cDiv.dimensions())

         getIt().on('resize', () => {
             dim= canvas.dimensions(cDiv.dimensions())


         })

         const wave = {
             y: dim.y / 1.5,
             length: 0.01,
             amplitude: 55,
             frequency: .07
         }

// g.add(wave, 'y', 0, dim.y)
//     g.add(wave, 'length', -0.01, 0.01)
//     g.add(wave, 'amplitude', -dim.y/2, dim.y/1.5)

//     g.add(wave, 'frequency', -1, 1)
//    g.destroy()
         let inc = wave.frequency;
         c.fillStyle  = 'rgba(0, 0, 0, 220.51, 1)'


         function colors(r, g, b, a=0.1) {
             this.r = r;
             this.b = b;
             this.g = g
             this.a= a
         }


         colors.prototype.update = function () {

             if(this.r>1500) this.r=0.01
             if(this.g>1500) this.g=0.01
             if(this.a>500) this.b=0.01
             if(this.a>500) this.a=0.01
             this.r+=0.01;
             this.a+=0.0001;
             this.b+=0.01
             this.g+=0.01

             return `hsl(${Math.abs(200 * Math.sin(this.r))}, ${Math.abs(50 *Math.sin(this.b) )}%, ${Math.abs(50 )}%)`

             // return `rgba(${Math.abs(255 * Math.sin(this.r))}, ${Math.abs(255 * Math.sin(this.g))}, ${Math.abs(255 * Math.sin(this.b))},
             // ${Math.abs(Math.sin(this.a))})`
         }
         c.fillStyle  = 'rgba(0, 0, 0,1)'
         let mili = new colors(25, 1, 189,0.2)
         c.font = "20px Georgia";
         function animate() {

             requestAnimationFrame(animate)

             c.clearRect(0, 0, dim.x, dim.y)
             c.beginPath();

             c.moveTo(-40, dim.y/2)
             for(let i =  -40; i<dim.x+10; i++) {

                 c.lineTo(i, (Math.abs(wave.y)) + Math.sin(i*wave.length+inc) * wave.amplitude*Math.sin(inc))
             }


             //  incL+=wave.length

             inc+=wave.frequency
             c.lineWidth= 10
             c.strokeStyle = mili.update()
             c.stroke()


         }

animate()
}

render() {
     return (
         <div className="canvas-div">
             <canvas id="canvas"></canvas>
         </div>
     )
}
}