import React ,{Component, Fragment}   from 'react'
import $ from 'jquery'
import {  Post } from 'react-axios'
import Loading from '../loaders/Loaders'
import valid from '../helpers/valid'
import Err from '../Errors/Errors'
import Job from './Job';
class Print extends Component {

    state = {
        email:'',
        location:'',
        drag: true,
        files:[],
        typeREX:/application\/pdf|application\/vnd\.openxmlformats-officedocument\.wordprocessingml\.document|application\/vnd\.ms-excel/,
        accept:'application/pdf,application/vnd.openxmlformats-officedocument.wordprocessingml.document,application/vnd.ms-excel',
        submitted:false,
        delivery:false,
        filenames:[],
        errors:[], sizes:0, tooLarge: false, submitting: false, mon: {}


    }
    onChange=(e)=> {
        this.setState({[e.target.id]: e.target.value})
    }
    inputChange = (e) =>{
    let files=  Array.from(e.target.files)
        this.onDrop(files, true)
}
    onDragOver = (e) => {
        e.preventDefault()
        $('.dropin').addClass('boxers')
    }
    onDragEnter =(e) => {
        e.preventDefault()
        $('.dropin').addClass('boxers')
    }
    onDragLeave = (e) => {
        e.preventDefault()
        $('.dropin').removeClass('boxers')
    }
    onDeliveryChange=() => {
        let {delivery} = this.state
        this.setState({delivery: !delivery})
    }
    onSubmit = (e)=> {
        e.preventDefault()
        // document.getElementById('fileser').remove()
        let {email, delivery, location, files} = this.state

        let mon = new FormData()
        mon.append('email', email)
        mon.append('location', location)
        mon.append('delivery', delivery.toString())

        files.forEach(i => {
            mon.append('file', i, i.name)
        })

        this.setState({submitting: true, mon:mon})



    }


    onDrop = (e, vile) =>{
       if(!vile) e.preventDefault();

        let    files = vile?e:  this.state.files, errors= this.state.errors
        if(!vile)  files =  files.concat(Array.from(e.dataTransfer.files))
        if(!files[0]) {
            $('.dropin').removeClass('boxers')
            this.setState({files: [], filenames:[]})
            return}


            let sizes, names, got=[], shorts=[];
        sizes = files.map(i => i.size).reduce((a, b) => a + b)
        console.log(sizes>this.props.MAX)


        got = files.map(i => {
            let short = /(\..+)/.exec(i.name)[0]
            shorts.push({
                short
            })


            if (i.type === "") return short
            return i.type
        }).filter((i, n) => valid(i, n, shorts, this.state.typeREX))



        if (got.length > 0) {

            errors = errors.concat(shorts.filter(i=> i.invalid).map(i=> {
                return {
                    message:`${i.short.replace('.','')} are not printable by our printer and will not be sent`
                }

            }))


            return setTimeout(()=>{
                files = files.filter(i=> {

                    return this.state.typeREX.test(i.type)
                })
                this.setState({errors: errors})
                this.onDrop(files, true)
            }, 2000)


        }
        names = files.map(i => i.name)

        names.map((i, n)=> {
            return {id:i+n, file: i}
        })

        let lard = sizes> this.props.MAX

        this.setState({files: files, errors: errors, filenames: names, sizes:sizes , tooLarge: lard})


    }

    onClick = (e) => {

        let files = this.state.files
        files.splice(e.target.dataset.index, 1)
        this.onDrop(files, true)
    }

    render() {

          if(!this.state.submitting)  return (
                <div className="flexer show">
                    <div className="card shadow-lg card-form " >
                        <div className="card-body">
                            <h5 className="card-title">Fill this</h5>
                            <h6 className="card-subtitle mb-2 text-muted">Make sure to indicate delivery</h6>
                            <form id="logs"  onSubmit={this.onSubmit} action="/api/job"  method="post" encType="multipart/form-data">
                                <div className="form-group">
                                    <label htmlFor="email">Email address</label>
                                    <input type="email" name="email" className="form-control" id="email" aria-describedby="emailHelp" placeholder="Enter email" onChange={this.onChange} value={this.state.email} />
                                        <small id="emailHelp" className="form-text text-muted">We'll contact you on this when its done.</small>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="location">Location</label>
                                    <input type="text" name="location" className="form-control" id="location" placeholder="Where you at"  onChange={this.onChange} value={this.state.location}/>
                                </div>

                                    <div className="form-group dropin">
                                        <div className="dropzone flexer"  id="dropper" style={{width: '100%', height: '150px'}} onDragOver={this.onDragOver} onDragEnter={this.onDragEnter} onDragLeave={this.onDragLeave} onDrop={this.onDrop}>
                                            <h2 className="text-secondary">DROP FILES</h2>
                                        </div>

                                        {this.state.filenames[0] && (
                                            <ul id="print-ul" className={'file-confirm'}>
                                                {this.state.filenames.map((i, n)=> (
                                                    <li className="lions show text-success" id={i+n} key={n+1}>{i} <span
                                                        className="btn text-danger"
                                                        style={{backgroundColor:'transparent', cursor:'pointer'}}
                                                        data-remove={i} data-index={n} onClick={this.onClick}>x</span></li>) )}
                                            </ul>
                                        )}
                                    </div>





                                <div className="form-group inder ">
                                    <label htmlFor="file">Select files</label>
                                    <input type="file"  onChange={this.inputChange} name="file" className="form-control-file" id="file" accept={this.props.accept || this.state.accept} multiple={true} />
                                </div>


                                <div className="form-checker" style={{margin:"10px"}}>

                                    <label className="form-check-label lo" htmlFor="delivery">

                                        Delivery

                                        <input name="delivery" type="checkbox" className="form-check-input" id="delivery" checked={this.state.delivery} onChange={this.onDeliveryChange} />
                                            <i></i>
                                    </label>
                                </div>
                                {this.state.delivery&& (
                                    <div className="explain"> <p>Delivery fee is N150</p>
                                        {this.state.delivery&&!this.state.location && (<p className={'text-danger'}>We need your <a className={"accent"} href={'/admin/privacy#location'}>Location</a> for delivery </p>)}</div>

                                )}
                                {this.state.tooLarge&&(
                                   <div className="errorDiv">
                                    <p className="text-danger erroer">{(this.state.sizes/(1024*1024)).toFixed(2)}MB is too large max is 5MB</p>
                                       <p className="text-danger">You can schedule larger jobs on the <a
                                           className="accent" href={"/admin/schedule?size="+ this.state.sizes}>Here</a></p>
                                   </div> )
                                  }
                                <button id="printer" disabled={this.state.tooLarge||!this.state.files[0]||!this.state.email||(this.state.delivery&&!this.state.location)}  type="submit" className="btn btn-primary">Submit</button>
                            </form>
                        </div>
                    </div>
                </div>
            )
        else {
              return (
                  <div>
                      <Post url={'/api/job'} data={this.state.mon}>
                          {(error, response, isLoading, makeRequest, axios) => {
                              if(error) {
                                  return (<div>{Err('Something Bad Happened and the file wasn\'t uploaded' , [error.message+ ' ' + error.code, 'You can Retry again'])} <button onClick={() => makeRequest({ params: { reload: true } })}>Retry</button></div>)
                              }
                              else if(isLoading) {
                                  return Loading()
                              }
                              else if(response !== null) {
                                  return (<Fragment>
                                      <Job job={response.data}/>
                                  </Fragment>)
                              }
                              return (<div>Default message before request is made.</div>)
                          }}

                      </Post>
                  </div>
              )
          }

    }
}

export default Print