import React, { Component } from 'react'
import moment from 'moment'
import SVGCircle from './SVGCircle';
import { mapNumber } from '../helpers/timeHelpers';



export class Setter extends Component {
    state = {
 
        hours: undefined,
        minutes: undefined,
        seconds: undefined
    }

    componentDidMount() {
        this.interval = setInterval(() => {
            const { timeTillDate } = this.props;
            const then = moment(timeTillDate);
            const now = moment();
            const countdown = moment(then - now);
           
            const hours = countdown.format('HH');
            const minutes = countdown.format('mm');
            const seconds = countdown.format('ss');
            this.setState({ hours, minutes, seconds })
        },1000)
    }
    componentWillUnmount() {
        if (this.interval) {
        clearInterval(this.interval);
        }
        }
    render() {
        const {  hours, minutes, seconds } = this.state;
        const hoursRadius = mapNumber(hours, 24, 0, 0, 360);
        const minutesRadius = mapNumber(minutes, 60, 0, 0, 360);
        const secondsRadius = mapNumber(seconds, 60, 0, 0, 360);
        console.log(hoursRadius)
        return (
            <div className='modal show flexer' onClick={this.props.toggleClasser} id={'modal' + this.props.idFile} >
            <div className="timer-Container" id={this.props.idFile}>
                <h1 className="light">Come in the next</h1>
                <div className="countdown-wrapper">
                    <div className="countdown-item light">

                    <SVGCircle raduis={hoursRadius}/>
                        {hours}
                        <span>hours</span>
                    </div>
                    <div className="countdown-item light">
                    <SVGCircle raduis={minutesRadius}/>
                        {minutes}
                        <span>minutes</span>
                    </div>
                    <div className="countdown-item light">
                    <SVGCircle raduis={secondsRadius}/>
                        {seconds}
                        <span>seconds</span>
                    </div>
                </div>
            </div>
            </div>
        )
    }
}

export default Setter
