import React,{Component,Fragment} from 'react'
import { Get } from 'react-axios';
import Job from './Job';
import uuid from 'uuid'
import Err from '../Errors/Errors'
import Loading from '../loaders/Loaders'
export default class JobsQuery extends Component{

    state ={
        email:'', location:'', name:'', byemail:true, byname:false, bylocation:false,submitting:false, param:''
    }

    onChange=(e)=>{
        let val= this.state[e.target.id]
        this.setState({[e.target.id]:!val})

    }
    onChangeText=(e)=>{
       
        this.setState({[e.target.id]:e.target.value })

    }
    onSubmit = (e)=>{
        e.preventDefault()
        let obj=[]

        Object.keys(this.state).forEach(i=>{
            if(i==='email'||i==='name'||i==='location'){
                if(this.state[i]) obj.push({ d:i, q:this.state[i]})
            }
        })

       obj= obj.map((i)=> {
          
             return  `${i.d}=${encodeURIComponent( i.q)}`
           
       } ).join('&&')
      // console.log(obj)
       this.setState({submitting:true,param:obj })
    }
    render() {
        let{email,name, location,byemail, byname,bylocation,submitting ,param }=this.state
     if(!submitting)   return (
            
            <div className="flexer show">

    <div className="card shadow-lg card-form">
        <div className="card-body">
             <h5 className="card-title"><i className="fas fa-search"></i>Check Job(s) status</h5>
                    <h6 className="card-subtitle mb-2 text-muted">If you can't find a valid job contact <a className="link accent" href="mailto:kosikenspears@gmail.com">Me</a></h6>

                    <form id="formLogin" onSubmit={this.onSubmit}>
                         <div className="form-checker" style={{margin:"10px"}}>
        
                            <label className="form-check-label lo" htmlFor="byemail">
        
                                Filter by email
        
                                <input onChange={this.onChange} name="byemail" type="checkbox"
                                    className="form-check-input" id="byemail" checked={byemail}/>
                                <i></i>
                            </label>
                        </div>
        <div className={"form-group name-div "+ (byemail? '':'hide nothing')}>
                            <label htmlFor="email">Email</label>
                            <input type="email" name="email" className="form-control" id="email" placeholder="eg person@email.com"  onChange={this.onChangeText} value={email} />
        
                        </div>
         <div className="form-checker" style={{margin:"10px"}}>
        
                            <label className="form-check-label lo" htmlFor="byname">
        
                                Filter by name
                               
                                <input  onChange={this.onChange} name="byname" type="checkbox"  className="form-check-input" id="byname" checked={byname}/>
                                <i></i>
                                </label>
                        </div>
 <div className={"form-group name-div "+ (byname? '':'hide nothing')}>
                            <label htmlFor="name">Filename</label>
                            <input type="text" name="name" className="form-control" id="name" placeholder="eg File.pdf"  onChange={this.onChangeText} value={name} />
        
                        </div>
                          <div className="form-checker" style={{margin:"10px"}}>
        
                            <label className="form-check-label lo" htmlFor="bylocation">
        
                                Filter by location
        
                                <input onChange={this.onChange} name="bylocation" type="checkbox" className="form-check-input" id="bylocation" checked={bylocation}/>
                                <i></i>
                            </label>
                        </div>
                         <div className={"form-group name-div "+ (bylocation ? '':'hide nothing')}>
                            <label htmlFor="location">Location</label>
                            <input type="text" name="location" className="form-control" id="location" placeholder="eg H301"  onChange={this.onChangeText} value={location}/>
        
                        </div>

                        <button id="loginForm" type="submit" className="btn btn-primary" disabled={!(email||location||name)}>Submit</button>
                    </form>
        </div>
    </div>
</div>

        )

        else{
            return (
                <Get url={'/api/jobquery?'+param} >
                {(error, response, isLoading, makeRequest, axios) => {
                    if(error) {
                        return (<div>{Err('Something Bad Happened and the file wasn\'t uploaded' , [error.message+ ' ' + error.code, 'You can Retry again'])} <button onClick={() => makeRequest({ params: { reload: true } })}>Retry</button></div>)
                    }
                    else if(isLoading) {
                        return Loading()
                    }
                    else if(response !== null) {
                        return (<Fragment>
                            {response.data?response.data.map(i=> (
                                <div  key={uuid()}>
                                    <Job job={i}/>
                                </div>
                            )):(
                                <div>error</div>
                            )}
                        </Fragment>)
                    }
                    return (<div>Default message before request is made.</div>)
                }}

            </Get>
        
            )
        }
    }
}
