import React, { Component } from 'react';
import Home from  './components/Home'


import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Header from './layouts/Header'
import Footer from './layouts/Footer'
import Canvas from './components/Canvas'
import Print from './components/Print'
import Pricing from './components/Pricing'
import JobsQuery from './components/JobsQuery'
import NoMatch from './Errors/ion'
class App extends Component {

 state={
     isDev:true,hoster:'http://localhost:8080', navOpen:false
 }


 toggleNav =() => {
     let {navOpen} = this.state

     this.setState({navOpen: !navOpen})
 }

    render() {

        return (
            <Router>

                {Header(this.state.navOpen, this.toggleNav)}

                <div className="hero root">
                    <div className="container" id="rooter">
                        <Switch>
                    <Route exact path={'/'}  render={(props) => {
                       return (<Home/>)
                    }}/>
                    <Route path={'/print'} render={(props) => {
                        return (<Print urel={this.state.hoster}  MAX={1024 * 1024 * 5}/>)
                    }}/>
                            <Route path={"/pricing"} component={Pricing} />
                     <Route path={'/jobs'} render={(props) => {
                        return (<JobsQuery urel={this.state.hoster} />)
                    }}/>
                        <Route path={"*"} component={NoMatch} />
                        </Switch>
                <Canvas/>
                </div>

                </div>
            <Footer/>
            </Router>

    );
    }
}

export default App;