import React from 'react'
import {Link} from 'react-router-dom'

export default (navOpen, toggleNav) => {
    return (
        <header className="header blue" id="header-home">
            <div className="container  flex">
                <div className="logo-div">
                    <a href="./" className="home-link">
                        <img src="965cc7af2b5badfb827a8814d9303adb.svg" alt="LOGO" id="home-logo" style={{width:'50px'}} />
                            <span className="large">PRINTSERV</span>
                    </a>
                    <button id="ham" onClick={toggleNav} className={"bgblue light p-2 rounded " + (navOpen? 'did': 'clo')}><i></i></button>

                </div>

                <nav className={"navbar navbar-light " + (navOpen? 'show': 'clo')}>
                    <ul className="nav">
                        <li className="nav-item"><Link to="./" id="home" className="nav-link">Home</Link></li>
                        <li className="nav-item"><a href="./printserve.html" id="about" className="nav-link">About</a>
                        </li>
                        <li className="nav-item"><Link to="/pricing" id="pricing" className="nav-link">Pricing</Link></li>
                        <li className="nav-item"><a href="/admin" className="nav-link" id="admin">Admin</a></li>
                        <li className="nav-item"><Link to="/jobs" className="nav-link" id="jobs">Find Job</Link></li>
                        <li className="nav-item"><Link to="/print" id="milirock" className="nav-link btn btn-primary">New Job</Link></li>
                    </ul>

                </nav>
                <div className="over" onClick={toggleNav}></div>
            </div>
        </header>
    )
}